



















import Vue from "vue";
import { Component, Prop, Watch, PropSync } from "vue-property-decorator";

@Component({
  components: {}
})
export default class Into extends Vue {
  now = new Date();
  monthSum = this.$store.getters.oneMonthSum(this.now);
  headerTitle = [
    { text: "", count: this.monthSum ? this.monthSum.expend : 0 },
    { text: "", count: this.monthSum ? this.monthSum.income : 0 },
    { text: "", count: this.monthSum ? this.monthSum.rest : 0 }
  ];
  monthText =
    this.now.getMonth() < 9
      ? "0" + (this.now.getMonth() + 1)
      : this.now.getMonth() + 1;
}
